import React, { useState, useEffect } from "react";
import MenhirNavigation from "./Navigation";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import "@fontsource/cabin/400.css";
import "@fontsource/eb-garamond/400.css";
import Footer from "./Footer";
import CookieConsent from "react-cookie-consent";
import { ChakraProvider, Box } from "@chakra-ui/react";

import MenhirAbout from "./About";
import MenhirHome from "./Home";
import MenhirProduct from "./Product";
import MenhirLogin, { MenhirPrivateRoute } from "./Login";
import { extendTheme, Heading } from "@chakra-ui/react";
import MenhirBrowser from "./Browser";
import MenhirCart from "./Cart";
import MenhirCheckoutShip from "./CheckoutShip";
import MenhirCheckoutPay from "./CheckoutPay";
import MenhirOrderCompleted from "./OrderCompleted";

const menhirTheme = extendTheme({
  colors: {
    brand: {
      100: "#498467",
      "100shade":"#5fa180",
      300: "#ffd470",
      900: "#622804",
    },
  },
  fonts: {
    heading: "Cabin",
    body: "Cabin",
  },
  shadows: {
    outline: "0px",
  },
  styles: {
    global: {
      body: {
        h1: {
          color: "#666666",
        },
        h3: {
          color: "#498467",
          fontWeight: "bold",
          textTransform: "uppercase",
          fontSize: "20px",
          marginTop: "30px",
          marginBottom: "30px",
          textAlign: "center",
        },
        h4: {
          color: "#666666",
          
        },
        input: { color: "#666666" },
        p: { color: "#666666" },
        label: { color: "#666666" },
        button: { borderRadius: "25px !important", minWidth:"100px !important" },
        select: { color: "#666666 !important", border:"1px solid #E2E8F0", borderRadius:"6px", height:"40px", width:"330px" },
      },
    },
  },
});

export const App = () => {
  const [data, setData] = useState([]);
  const [scrolled, setScrolled] = useState([]);

  const handleScroll = () => {
    if (window.scrollY === 0) {
      setScrolled(false);
    } else {
      setScrolled(true);
    }
  };

  const getData = () => {
    fetch("./test-data-items.json", {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
      },
    })
      .then(function (response) {
        return response.json();
      })
      .then(function (myJson) {
        setData(myJson);
      });
  };

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
    getData();
  }, []);

  return (
    <Router>
      <ChakraProvider theme={menhirTheme}>
        <Box
          ml={{ base: "0", md: "0", lg: "0", xl: "230px" }}
          mr={{ base: "0", md: "0", lg: "0", xl: "230px" }}
          pt={{ base: "2", md: "6" }}
          pb={{ base: "2", md: "6" }}
          onScroll={handleScroll}
        >
          <MenhirNavigation navBarScrolled={scrolled} />

          <Switch>
            <Route exact path="/browser" component={MenhirBrowser} />
            <Route path="/about" component={MenhirAbout} />
            <Route path="/product" component={MenhirProduct} />
            <Route path="/cart" component={MenhirCart} />
            <MenhirPrivateRoute path="/checkoutpay">
              <MenhirCheckoutPay />
            </MenhirPrivateRoute>
            <MenhirPrivateRoute path="/checkoutship">
              <MenhirCheckoutShip />
            </MenhirPrivateRoute>
            <MenhirPrivateRoute path="/ordercompleted">
              <MenhirOrderCompleted />
            </MenhirPrivateRoute>            
            <Route path="/login" component={MenhirLogin} />
            <Route path="/" component={MenhirHome} />
          </Switch>
          <Footer />
        </Box>
        <CookieConsent
          enableDeclineButton
          flipButtons
          location="bottom"
          buttonText="I Accept"
          cookieName="myAwesomeCookieName2"
          style={{
            background: "#fafafa",
            height: "220px",
            color: "#666666",
            padding: "0 30px 0 30px",
          }}
          buttonStyle={{
            background: "#319795",
            color: "#ffffff",
            fontSize: "16px",
            width: "150px",
          }}
          declineButtonStyle={{
            background: "#ff0000",
            color: "#ffffff",
            fontSize: "16px",
            width: "150px",
          }}
          expires={150}
          overlay="true"
        >
          <div style={{ fontSize: "20px", fontWeight: "bold" }}>
            Cookie consent
          </div>
          This website uses cookies to enhance the user experience by saving
          which products you like. If you aren't happy with this, please click I
          Decline. The website will work anyway, but with a limited shopping
          experience.
        </CookieConsent>
      </ChakraProvider>
    </Router>
  );
};
