import { Link } from "react-router-dom";
import { useDispatch } from "react-redux";
import { cartActions } from "./store/index";
import { useSelector } from "react-redux";
import "./Checkout.css";
import {
  Center,
  Box,
  Text,
  Image,
  SimpleGrid,
  Flex,
  Button,
} from "@chakra-ui/react";
import thanksImage from "./thanksImage.png";
import thanksImageSmall from "./thanksImage-small.png";
import { FaCheckSquare } from "react-icons/fa";

export default function MenhirOrderCompleted(props: any) {
  const dispatch = useDispatch();
  const cart = useSelector((state) => state.cart);
  const itemCount = cart.length;
  const totalItems = cart.reduce((acc, val) => {
    const discount = val.Discount;
    const discountedPrice = val.Price - (val.Price * discount) / 100;
    return acc + discountedPrice;
  }, 0);
  const addressLineOne = "Michel De Vinci";
  const addressLineTwo = "35, rue Galande PARIS";
  const addressLineThree = "FRANCE";

  const flushCart = () => {
    dispatch(cartActions.clear({}));
  };

  return (
    <Flex>
      <Box flex="1" p={0}>
        {cart && cart.length > 0 && (
          <Text as="h3" mt={{ base: 0, md: 5 }} mb={{ base: 0, md: 10 }}>
            ORDER CONFIRMED
          </Text>
        )}

        {cart && cart.length > 0 && (
          <SimpleGrid columns={2} spacing={5} minChildWidth="300px" p={2}>
            <Box className="menhir-input-form">
              <SimpleGrid columns={3} minChildWidth="220px">
                <Box>
                  <FaCheckSquare
                    style={{ display: "inline", color: "#4fc4ac" }}
                  />
                  <Text
                    as="p"
                    mt={2}
                    mb={2}
                    ml={2}
                    style={{ display: "inline" }}
                  >
                    Your order contains
                  </Text>
                  <Text
                    as="p"
                    mt={2}
                    mb={2}
                    ml={2}
                    fontSize="20px"
                    fontWeight="bold"
                  >
                    {itemCount} items
                  </Text>
                </Box>
                <Box>
                  <FaCheckSquare
                    style={{ display: "inline", color: "#4fc4ac" }}
                  />
                  <Text
                    as="p"
                    mt={2}
                    mb={2}
                    ml={2}
                    style={{ display: "inline" }}
                  >
                    Your order will be sent to
                  </Text>
                  <Text
                    as="p"
                    mt={2}
                    mb={2}
                    ml={2}
                    fontSize="20px"
                    fontWeight="bold"
                  >
                    {addressLineOne}
                  </Text>
                  <Text as="p" mt={2} mb={2} ml={2} fontWeight="bold">
                    {addressLineTwo}
                  </Text>
                  <Text as="p" mt={2} mb={2} ml={2} fontWeight="bold">
                    {addressLineThree}
                  </Text>
                </Box>
                <Box>
                  <FaCheckSquare
                    style={{ display: "inline", color: "#4fc4ac" }}
                  />
                  <Text
                    as="p"
                    mt={2}
                    mb={2}
                    ml={2}
                    style={{ display: "inline" }}
                  >
                    Estimated delivery
                  </Text>
                  <Text
                    as="p"
                    mt={2}
                    mb={2}
                    ml={2}
                    fontSize="20px"
                    fontWeight="bold"
                  >
                    3-5 days
                  </Text>
                </Box>
              </SimpleGrid>
            </Box>
            <Box>
              <Center>
                <Image
                  display={{ base:'block', md:'none'}}
                  src={ thanksImageSmall }
                  alt="Thank you for ordering"
                />
                <Image
                  display={{ base:'none', md:'block'}}
                  src={thanksImage }
                  alt="Thank you for ordering"
                />                
              </Center>
            </Box>
          </SimpleGrid>
        )}

        <Center mt={4}>
          <Link to="/">
            <Button
              onClick={flushCart}
              mt={10}
              mb={10}
              bg="brand.100"
              color="#ffffff"
              _hover={{ bgColor: "brand.100shade"}}
              size="md"
              fontWeight="normal"
            >
              CONTINUE SHOPPING
            </Button>
          </Link>
        </Center>
      </Box>
    </Flex>
  );
}
